import React, { useState } from "react";
import "./App.css";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Header from "./Components/Header/Header";
import Footer from "./Components/Footer/Footer";
import Accueil from "./Components/Accueil/Accueil";
import "bootstrap/dist/css/bootstrap.min.css";
import Apropos from "./Components/Apropos/Apropos";
import Cv from "./Components/Cv/Cv";
import Mesprojets from "./Components/Mesprojets/Mesprojets";



function App() {
  
  const [viewBackground, setViewBackground] = useState(false);

  function bgColor() {
    if (viewBackground) {
      return (
        <div className="header-nav">
          <Header />
        </div>
      );
    } else {
      return (
        <div
          className="header-nav-background">
          <Header />
        </div>
      );
    }
  }

  return (
    <Router>
      <div onScroll={() => {
            setViewBackground(viewBackground);
          }}>
      {bgColor()}
      <div className="main-content">
        <Switch>
          <Route path="/" component={Accueil} exact />
          <Route path="/apropos" component={Apropos} exact />
          <Route path="/cv" component={Cv} exact />
          <Route path="/mesprojets" component={Mesprojets} exact />
        </Switch>
      </div>
      <Footer />
      </div>
    </Router>
  );
}

export default App;
