import React from "react";
import "./accueil.css";
import Carousel from "react-bootstrap/Carousel";
import Fade from "react-reveal/Fade";

function Accueil() {
  return (
    <section>
      <div className="accueil-main-content">
        <Fade left>
          <Carousel className="carousel-main">
            <Carousel.Item>
              <img
                className="d-block w-100"
                src="/img/mika-3.png"
                alt="First slide"
              />
            </Carousel.Item>
            <Carousel.Item>
              <img
                className="d-block w-100"
                src="/img/sophie-3.png"
                alt="second slide"
              />
            </Carousel.Item>
            <Carousel.Item>
              <img
                className="d-block w-100"
                src="/img/jashoppan-3.png"
                alt="Third slide"
              />
            </Carousel.Item>
          </Carousel>
        </Fade>
      </div>
      <p>t</p>
      <p>t</p>
      <p>t</p>
      <p>t</p>
      <p>t</p>
      <p>t</p>
      <p>t</p>
      <p>t</p>
      <p>t</p>
    </section>
  );
}

export default Accueil;
