import React from "react";
import "./apropos.css";
import Slide from 'react-reveal/Slide';
import Rotate from 'react-reveal/Rotate';


function Apropos() {
  return (
    <section>
        <div className="apropos-container">
          <div className="personal">
          <Slide left>
            <img src="/img/tre.png" alt ="pic0"/>
            </Slide>
          </div>
          <div className="personal">
          <Slide right>
            <h2>Je m'appelle Cyril</h2>

            <p>En couple avec un enfant de 8 ans, j'aime les animaux, la nature ainsi que la mécanique.</p>
            <p>Passionné par le web, les nouvelles technologies, je prends plaisir à me lever chaque jours pour coder et apprendre.</p>
            <p>Je me documente beaucoup dans le but d'apprendre des nouvelles technos et me permettre de proposer les meilleures solutions pour répondre aux cahiers des charges.</p>
            </Slide>
            </div>
        </div>
        <div className="technos">
            <Rotate bottom right>
            <img src="/img/html-css-js.png" alt="pic"/>
            </Rotate>
            <Slide bottom>
            <img src="/img/react.png" alt="pic1"/>
            </Slide>
            <img src="/img/react-native.png" alt="pic2"/>
            <Slide bottom>
            <img src="/img/node_express.png" alt="pic3"/>
            </Slide>
            <Rotate bottom right>
            <img src="/img/mongodb.png" alt="pic4"/>
            </Rotate>
        </div>
        <div className="technos">
            <Rotate top right>
            <img src="/img/github.png" alt="pic"/>
            </Rotate>
            <Slide top>
            <img src="/img/gitlab.png" alt="pic1"/>
            </Slide>
            <img src="/img/postman.png" alt="pic2"/>
            <Slide top>
            <img src="/img/Robot-3t.png" alt="pic3"/>
            </Slide>
            <Rotate top right>
            <img src="/img/mongodb.png" alt="pic4"/>
            </Rotate>
        </div>

    </section>
  );
}

export default Apropos;
